<template>
    <div class='appointLea'>
       <div class='top'>
           <span class='fundInfo' @click='goback()'>团课管理</span>
           <img src='../../assets/images/icon_nav_arrow.png'/>
           <span class='branch'>团课预约限制</span>
       </div>
       <div class='setting'>
           <div class='btnlist'>
               <div class='edit cancel' @click="cancel" v-if='edit'>
                   <span>取消</span>
               </div>
               <div class='edit submit' @click="submit" v-if='edit'>
                   <span>提交</span>
               </div>
               <div class='edit' @click="editInfo" v-if='!edit'>
                   <i  class='iconfont icon-icon_edit_blue'></i>
                   <span>编辑</span>
               </div>
           </div>
           <div class='content'>
               <el-form :rules="rules" :model="form">
                   <el-form-item label="在课程开始前多少分钟不能预约：">
                        <el-input  v-if='edit' v-model='form.reservationGroupEndTime' :maxlength="3"></el-input>
                        <div v-if='!edit' class='el-input'>{{form.reservationGroupEndTime}}</div>
                        <span class='minute'>分钟</span>
                    </el-form-item>
                    <el-form-item label="在课程开始前多少分钟不能取消预约：">
                        <el-input  v-if='edit' v-model='form.reservationCancelGroupEndTime' :maxlength="3"></el-input>
                        <div v-if='!edit' class='el-input'>{{form.reservationCancelGroupEndTime}}</div>
                        <span class='minute'>分钟</span>
                    </el-form-item>
               </el-form>
           </div>
       </div>
    </div>
</template>
<script>
import {getConfig, addConfig} from '../../services/service.js'
import {isApiResponseOk} from '../../utils/com.js'
export default {
    data () {
        return {
            edit: false, // 控制是都是编辑状态
            form: {
                reservationGroupEndTime: '',
                reservationCancelGroupEndTime: '',
            },
            rules: {
              reservationGroupEndTime: [
                {required: false},
                {type: 'number', message: '时间必须为整数'}
              ],
              reservationGroupEndTime: [
                {required: false},
                {type: 'number', message: '时间必须为整数'}
              ]
            }
        }
    },
    watch: {
        'form.reservationGroupEndTime': function (newVal, oldVal) {
            let FormatReg = /^\d+$/
            if (!FormatReg.test(newVal)) return
            if (this.form.reservationGroupEndTime.length) {
                if (FormatReg.test(newVal)) {
                    this.form.reservationGroupEndTime = newVal
                } else {
                    this.form.reservationGroupEndTime = oldVal
                }
            }
        },
        'form.reservationCancelGroupEndTime': function (newVal, oldVal) {
            let FormatReg = /^\d+$/
            if (!FormatReg.test(newVal)) return
            if (this.form.reservationCancelGroupEndTime.length) {
                if (FormatReg.test(newVal)) {
                    this.form.reservationCancelGroupEndTime = newVal
                } else {
                    this.form.reservationCancelGroupEndTime = oldVal
                }
            }
        }
    },
    created () {
        this.initInfo()
    },
    methods: {
        editInfo () {
            this.edit = !this.edit
        },
        async initInfo () {
            const res = await getConfig()
            if (isApiResponseOk(res) && res.data) {
                this.form = res.data
            } else {
                this.form.reservationGroupEndTime = ''
                this.form.reservationCancelGroupEndTime = ''
            }
        },
        async submit () {
            this.form.courseType = 'GROUP'
            const res = await addConfig(this.form)
            if (isApiResponseOk(res)) {
                this.$message({
                    showClose: true,
                    message: '更新成功',
                    type: 'success'
               })
               this.edit = false
            }
        },
        cancel () {
            this.initInfo()
            this.edit = false
        },
        // 返回到团课种类页面
        goback () {
            this.$router.push({name: 'leaguekind'})
        },
    }
}
</script>
<style lang="scss">
.appointLea{
    .top{
       .fundInfo{
            font-size: .17rem;
            color: #8597A1;
            cursor: pointer;
       }
       .branch{
           font-family: PingFangSC-Semibold;
           font-size: .21rem;
           color: #263238;
       } 
    }
    .setting{
        background: #fff;
        margin-top: .3rem;
       .btnlist{
            display: flex;
            height: .6rem;
            justify-content: flex-end;
            align-items: center;
            padding-right: .2rem;
            border-bottom: 1px solid rgba(216,216,216,.3);
           .edit{
               border: 1px solid #E9ECEF;
               border-radius: 4px;
               width: 1rem;
               height: .36rem;
               line-height: .36rem;
               font-size: .16rem;
               color: #20c6ba;
               text-align: center;
               cursor: pointer;
           }
           .cancel{
               color: #8597A1;
               margin-right: .12rem;
           }
           .submit{
               color: #FFFFFF;
               background-image: linear-gradient(138deg, #00DEAF 0%, #00DDB0 19%, #00B1BF 100%);
           }
       }
       .content{
            padding: .3rem .5rem;
            .el-form{
                .el-form-item{
                    display: flex;
                    background: #FFFFFF;
                    border: 1px solid #EAECEF;
                    width: 4.7rem;
                    padding-left: .2rem;
                    padding-right: .2rem;
                    .el-form-item__label{
                        font-family: PingFangSC-Regular;
                        font-size: .14rem;
                        color: #8597A1;
                        letter-spacing: 0;
                        white-space: nowrap;
                    }
                    .el-form-item__content{
                        display: flex;
                        align-items: center;
                        flex: 1;
                        text-align: right;
                        .el-input{
                            padding-right: 14px;
                            width: 95% !important;
                            .el-input__inner{
                                border: none !important;
                                text-align: right;
                                padding: 0px;
                            }
                        }
                        .minute{
                            flex: 1;
                            white-space: nowrap;
                        }
                    }
                }
            }
       }
    }
}
</style>





